.link {
    text-decoration: none;
    color: #77aaff;
    margin: 10px;
}

.link:hover {
    color: #cc99ff;
}

.active {
    color: #55ffff;
}

.block {
    width: 21px;
    height: 21px;

    margin: 10px;

    background: #99ff99;
}

.header {
    display: flex;

    width: 280px;

    transition-duration: 500ms;
    transform: translate(-220px);
}

.header:hover {
    transition-duration: 500ms;
    transform: translate(0);
}